<template>
  <div class="page">
    <div class="page__content">
      <div class="page__header">
        <div class="page__header-wrap">
          <h1 class="page__title">
            Афиша событий
          </h1>
          <button
            class="filters__btn-icon page__filter-btn"
            @click="showFilters = true"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="16"
              height="14"
              fill="none"
              viewBox="0 0 16 14"
            >
              <path
                fill="#fff"
                fill-rule="evenodd"
                d="M.962 2.128V3.57c0 .13.047.255.131.353l4.157 4.85a.18.18 0 01.043.117v3.703a1.262 1.262 0 001.75 1.166l2.886-1.203c.471-.196.778-.656.778-1.166v-2.5a.18.18 0 01.043-.117l4.157-4.85a.54.54 0 00.13-.353V2.128c0-.526-.209-1.032-.58-1.404a1.989 1.989 0 00-1.404-.581H2.947c-.526 0-1.031.21-1.404.58a1.988 1.988 0 00-.58 1.405zM13.32 4.113H2.681l3.391 3.956c.196.229.304.52.304.822v3.703a.18.18 0 00.25.167l2.887-1.203a.181.181 0 00.111-.167v-2.5c0-.301.108-.593.304-.822l3.391-3.956zm.636-1.083v-.902a.904.904 0 00-.902-.902H2.947a.904.904 0 00-.902.902v.902h11.91z"
                clip-rule="evenodd"
              />
            </svg>
          </button>
        </div>
      </div>
      <div class="page__body">
        <div class="page__body-sidebar">
          <div
            class="filter-sidebar"
            @click.self="showFilters = false"
            :class="{ 'filter-sidebar--show': showFilters }"
          >
            <div class="filter-sidebar__container">
              <button
                @click="$router.push('/create-event')"
                class="button-primary mb-20"
                v-if="isResident || isAdministrator"
              >
                Добавить свое событие
              </button>
              <filters
                @filter-list="onFilter"
                @filter-reset="filterReset"
                :filter-type="'event'"
              />
            </div>
          </div>
        </div>
        <div class="page__body-content">
          <div
            class="playbill-category"
            v-for="(eventsItem, index) in events"
            :key="index"
            v-show="eventsItem.events._items.length > 0"
          >
            <div
              class="playbill__title"
              v-if="eventsItem.events._items.length > 0"
            >
              {{ eventsItem.category.title }}
            </div>
            <div class="playbill-cards">
              <event-card
                :event="event"
                v-for="event in eventsItem.events._items"
                :key="event.id"
                :id="event.id"
                @event-set-favorite="onSetFavorite(event, $event)"
                @event-delete-favorite="onDeleteFavorite(event, 0)"
                :review="
                  !event.moderated &&
                    event.author_id == getUser.id &&
                    (isResident || isAdministrator)
                "
                :edit="
                  event.author_id == getUser.id &&
                    (isResident || isAdministrator)
                "
              />
            </div>
          </div>
          <!--          <button-->
          <!--            class="playbill__more-btn button-warning"-->
          <!--            v-show="showMore"-->
          <!--            @click="loadMore"-->
          <!--          >-->
          <!--            показать ещё-->
          <!--          </button>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EventCard from "../components/Cards/EventCard";
import Filters from "../components/Filters";
import { mapGetters } from "vuex";
import axios from "axios";

export default {
  name: "PlaybillPage",
  components: { Filters, EventCard },
  data() {
    return {
      bookmark: false,
      events: [],
      pageData: [],
      showFilters: false,
      filters: {
        category: [],
        dateRange: null
      },
      currentPage: 1,
      perPage: 20
    };
  },
  mounted() {
    this.loadData();
  },
  computed: {
    ...mapGetters("user", ["isResident", "isAdministrator", "getUser"]),
    showMore() {
      return this.pageData.pageCount > this.pageData.currentPage;
    }
  },
  methods: {
    async loadData() {
      let load = this.$loading.show();

      let url = new URL(
        `${process.env.VUE_APP_API_URL}/v1/event/category-groups`
      );
      // url.searchParams.set("per-page", this.perPage);
      // url.searchParams.set("page", this.currentPage);

      if (this.filters && Array.isArray(this.filters.category)) {
        this.filters.category.forEach(item =>
          url.searchParams.append("category_id[]", item)
        );
      }

      if (this.filters.dateRange) {
        url.searchParams.append(
          "begin_time[from]",
          this.filters.dateRange.start
        );
        url.searchParams.append("begin_time[to]", this.filters.dateRange.end);
      }

      await axios
        .get(url)
        .then(response => {
          this.events = response.data;
          load.hide();
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
            load.hide();
          }
          this.$toasted.global.error(error.response.data.message);
          load.hide();
        });
    },
    loadMore() {
      this.currentPage++;
      this.loadData();
    },
    onFilter(filter) {
      this.currentPage = 1;
      this.filters.category = filter.category;
      this.filters.dateRange = filter.dateRange;
      this.showFilters = false;
      this.loadData();
    },
    filterReset() {
      this.currentPage = 1;
      this.filters.category = [];
      this.filters.dateRange = null;
      this.showFilters = false;
      this.loadData();
    },
    onSetFavorite(event, payload) {
      this.updateEventsFavorite(event, payload);
    },
    onDeleteFavorite(event, payload) {
      this.updateEventsFavorite(event, payload);
    },
    updateEventsFavorite(event, payload) {
      this.events.forEach(groupEvents => {
        groupEvents.events._items.forEach(itemEvent => {
          if (itemEvent.id === event.id) itemEvent.is_favorite = payload;
        });
      });
    }
  }
};
</script>

<style scoped></style>
